import * as React from 'react';
import {
  Box,
  createTheme,
  Grid,
  ThemeProvider,
  Typography
} from '@mui/material';

export default function MainScreen() {
  const theme = createTheme({
    palette: {
      primary: {
        main: "#F8BC24",
      },
    },
  });
  return (
    <>
      <ThemeProvider theme={theme}>
        <Box sx={{ 
          borderTopLeftRadius: '20px',
          borderTopRightRadius: '20px',
          bgcolor: 'primary.main',
          marginBottom:'0px',
          paddingTop:'5px',
          paddingBottom:'5px',
          paddingLeft:'10px',
          paddingRight:'10px',
          }}>
            <Grid container justifyContent='space-between'>
              <Grid item display='flex'>
                <Typography>Oilers&nbsp;&nbsp;$15,0000</Typography>
              </Grid>
              <Grid item display='flex'>
                <Typography align='right'>Round 1/30</Typography>
              </Grid>
            </Grid>
        </Box>
        <Box sx={{
          borderBottomLeftRadius: '20px',
          borderBottomRightRadius: '20px',
          marginTop:'0px',
          bgcolor: 'white',
          height:'525px'
          }}>
            <Grid container>
              <Grid item>
                {/* map */}
              </Grid>
              <Grid item>
                {/* details */}
                {/* buttons */}
              </Grid>
            </Grid>
        </Box>
      </ThemeProvider>
    </>
  );
}
