import './App.css';
import { Container, createTheme, CssBaseline, ThemeProvider } from '@mui/material';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import MainScreen from './components/mainScreen/mainScreen.js';

function App() {
  const bgTheme = createTheme({
    palette: {
      background: {
        default: '#1A4645'
      },
      custom: {
        green: '#1A4645',
      }
    }
  })

  return (
    <ThemeProvider theme={bgTheme}>
      <CssBaseline/>
      <div className="App">
        <Container maxWidth="lg" sx={{ padding:'10px'}}>
          <MainScreen/>
        </Container>
      </div>
    </ThemeProvider>
  );
}

export default App;
